function recopen(){  //
	// var type=$("[name=type]:checked").val();
	// var bit=+$(".bit").val();
  // var sample=+$(".sample").val();
  var type = "mp3"; // 
  var bit = 16; // kbps mp3wav816
  var sample = 16000; // mp3wav

