function recstop(batCall){
	console.log('batCall:', batCall);
	if(rec){
		if(!batCall){
			console.log(""+rec.set.type+"...");
		}
		rec.stop(
		function(blob,time){
      console.log('blob:=======', blob);
			console.log('time:=======', time);
      down(blob, new Date.getTime(), 'mp3')
		},function(s){
			console.log(""+s);
			batCall&&batCall();
		});
	}
